// Servio para lidar com notificaes
const notificationService = {
  // Verifica o status atual da permisso de notificaes
  checkPermissionStatus() {
    if (!('Notification' in window)) {
      return 'unsupported';
    }
    return Notification.permission; // 'granted', 'denied' ou 'default'
  },
  
  // Solicita permisso para enviar notificaes - deve ser chamada em resposta a uma interao do usurio
  async requestPermission() {
    if (!('Notification' in window)) {
      console.log('Este navegador no suporta notificaes');
      return false;
    }
    
    if (Notification.permission === 'granted') {
      return true;
    }
    
    if (Notification.permission !== 'denied') {
      try {
        const permission = await Notification.requestPermission();
        return permission === 'granted';
      } catch (error) {
        console.error('Erro ao solicitar permisso para notificaes:', error);
        return false;
      }
    }
    
    return false;
  },
  
  // Envia uma notificao
  async sendNotification(title, options = {}) {
    // Verifica se notificaes so suportadas e permitidas
    if (!('Notification' in window)) {
      console.log('Este navegador no suporta notificaes');
      return;
    }
    
    // Se a permisso no foi concedida, tenta solicitar
    if (Notification.permission !== 'granted') {
      const granted = await this.requestPermission();
      if (!granted) {
        console.log('Permisso para notificaes negada');
        return;
      }
    }
    
    // Configuraes padro da notificao
    const defaultOptions = {
      icon: '/logo192.png',
      badge: '/logo192.png',
      vibrate: [200, 100, 200],
      ...options
    };
    
    // Cria e envia a notificao
    try {
      const notification = new Notification(title, defaultOptions);
      
      // Configura ao ao clicar
      if (options.onClick) {
        notification.onclick = options.onClick;
      } else {
        notification.onclick = () => {
          window.focus();
          notification.close();
        };
      }
      
      return notification;
    } catch (error) {
      console.error('Erro ao enviar notificao:', error);
    }
  },
  
  // Notificao de chamada bloqueada
  async notifyBlockedCall(phoneNumber) {
    return this.sendNotification(
      'Chamada Bloqueada', 
      {
        body: `O nmero ${phoneNumber} foi bloqueado pela proteo Quero Paz.`,
        tag: 'blocked-call',
        data: { phoneNumber, type: 'blocked' }
      }
    );
  },
  
  // Notificao de chamada silenciada
  async notifySilencedCall(phoneNumber) {
    return this.sendNotification(
      'Chamada Silenciada', 
      {
        body: `O nmero ${phoneNumber} foi silenciado.`,
        tag: 'silenced-call',
        data: { phoneNumber, type: 'silenced' }
      }
    );
  },
  
  // Notificao de chamada para confirmao
  async notifyPendingConfirmation(phoneNumber) {
    return this.sendNotification(
      'Confirma pra Mim', 
      {
        body: `Nova chamada pendente de confirmao: ${phoneNumber}`,
        tag: 'pending-confirmation',
        data: { phoneNumber, type: 'confirm' }
      }
    );
  }
};

export default notificationService;
